<template>
	<div id="app" :class="bgShow?'bg-show':''">
		<!-- 顶部导航 -->
		<!-- <top-nav /> -->
		<div class="top-nav">
			<van-row>
				<van-col span="3">
					<img class="image menu" src="./assets/images/icon-menu@2x.png" @click="onMenu" />
					<div class="bg" v-show="bgShow" @click="bgShowFunc(false)">
						<ul @click.stop="bgShowFunc(true)" :class="[ulShow?'ul-show':'ul-none']">
							<li v-for="(item,index) in menuArr" :class="[menuActive===index?'menu-active':'']" :key='index'>
								<span @click.stop="bgShowFunc(false,index)">
									<a :href="item.href" v-text="item.name"></a>
								</span>
							</li>
						</ul>
					</div>
				</van-col>
				<van-col span="19">
					<van-image :src="require('./assets/images/logo-2x.png')" />
					<strong>北京翔辉华通置业集团有限公司</strong>
				</van-col>
				<van-col span="2">
					<van-image :src="require('./assets/images/icon-phone-2x.png')" />
				</van-col>
			</van-row>
		</div>
		<div class="welcome">
			<div class="container">
				<h2>
					<p>欢迎来到</p>
					<p>北京翔辉华通置业集团有限公司</p>
				</h2>
				<div class="eng">Welcome to Beijing Xianghui Huatong Real Estate Group Co., Ltd</div>
				<van-button round type="default">联系我们</van-button>
			</div>

		</div>
		<!-- 项目类型 -->
		<project-type id="project-type" />
		<!-- 项目展示 -->
		<!-- <test />
		<hr /> -->
		<reveal id="reveal" />
		<!-- 企业介绍 -->
		<introduces id="introduces" />
		<!-- 架构管理 -->
		<manages id="manages" />
		<!-- 集团子公司 -->
		<subsidiary id="subsidiary" />
		<!-- 底部 -->
		<project-footer id="about" />
	</div>
</template>

<script>
	// import topNav from "./components/topNav.vue"
	import projectType from "./components/projectType.vue"
	import reveal from "./components/reveal.vue"
	import introduces from "./components/introduces.vue"
	import manages from "./components/manages.vue"
	import subsidiary from "./components/subsidiary.vue"
	import projectFooter from "./components/projectFooter.vue"
	import test from "./components/test.vue"
	import $ from 'jquery'
	export default {
		name: 'app',
		components: {
			// topNav,
			projectType,
			reveal,
			introduces,
			manages,
			subsidiary,
			projectFooter,
			test
		},
		data() {
			return {
				activeKey: 0,
				bgShow: false,
				ulShow: false,
				menuActive: 0,
				clickNum: 0,
				menuArr: [{
						href: "#introduces",
						name: '企业介绍'
					},
					{
						href: "#manages",
						name: '管理架构'
					},
					{
						href: "#project-type",
						name: '经营业务'
					},
					{
						href: "#reveal",
						name: '项目展示'
					},
					{
						href: "#subsidiary",
						name: '集团子公司'
					},
					{
						href: "#about",
						name: '联系我们'
					},
				]
			};
		},
		methods: {
			// onAnchor() {},
			// onChange(index) {
			// 	Notify({
			// 		// type: 'primary',
			// 		message: index
			// 	});
			// },
			onMenu() {
				this.clickNum++
				if (this.clickNum === 1) {
					if (this.bgShow) {
						this.ulShow = !this.ulShow
						setTimeout(() => {
							this.bgShow = !this.bgShow
							this.clickNum = 0
						}, 1500)
					} else {
						this.bgShow = !this.bgShow
						this.ulShow = !this.ulShow
						this.clickNum = 0
					}
				}
			},
			bgShowFunc(bool, index) {
				this.clickNum++
				if (typeof(index) === 'number') {
					this.menuActive = index
					this.bgShow = bool
					this.ulShow = bool
					this.clickNum = 0
				} else {
					if (this.clickNum === 1) {
						if (!bool) {
							this.ulShow = bool
							setTimeout(() => {
								this.bgShow = bool
								this.clickNum = 0
							}, 1500)
						} else {
							this.bgShow = bool
							this.ulShow = bool
							this.clickNum = 0
						}
					}
				}
			}

		}
	}
</script>

<style lang="scss">
	.container {
		width: 92%;
		margin: 0 auto;
	}

	#app {
		width: 100%;

		.welcome {
			background-image: url(assets/images/banner@3x.png);
			background-size: cover;
			background-repeat: no-repeat;

			h2>p,
			.eng {
				color: #fff;
			}

			h2 {
				font-size: 22px;
				padding-top: 100px;
			}

			.eng {
				width: 800px;
				transform: scale(0.6);
				text-transform: uppercase;
				margin-left: -159px;
				color: rgba(255, 255, 255, 0.72);
			}

			.van-button {
				height: 35px;
			}

			.van-button--normal {
				padding: 0 30px;
				font-size: 12px;
				font-weight: bold;
				margin-top: 19px;
				margin-bottom: 91px;
			}
		}

		.top-nav {
			padding: 20px 15px 20px 13px;
			background: #06254F;
			color: #FFFFFF;

			.van-image {
				width: 25px;
				height: 25px;
				vertical-align: middle;
			}

			strong {
				font-size: 13px;
				vertical-align: middle;
				margin-left: 5px;
				letter-spacing: 0;
			}

			.van-col--3 {
				height: 34px;
				margin-top: -11px;

				.image {
					width: 25px;
					margin-top: 12px;
					margin-left: 10px;
					display: none;
				}

				.menu {
					display: block;
				}

				.close {
					display: block;
				}

				@keyframes mybgShow {
					from {
						height: 0;
					}

					to {
						height: 100vh;
					}
				}

				@-webkit-keyframes mybgShow

				/* Safari 与 Chrome */
					{
					from {
						height: 0;
					}

					to {
						height: 100vh;
					}
				}

				@keyframes mybgNone {
					from {
						height: 100vh;
					}

					to {
						height: 0;
					}
				}

				@-webkit-keyframes mybgNone

				/* Safari 与 Chrome */
					{
					from {
						height: 100vh;
					}

					to {
						height: 0;
					}
				}

				.bg {
					position: fixed;
					width: 100%;
					height: 100vh;
					left: 0;
					top: 65px;
					background-color: rgba(0, 0, 0, 0.6);
					border-top: 4px solid #000;
					z-index: 999;

					.ul-show {
						animation: mybgShow 1.2s;
						-webkit-animation: mybgShow 1.2s;
						height: 100vh;
					}

					.ul-none {
						animation: mybgNone 1.2s;
						-webkit-animation: mybgNone 1.2s;
						height: 0;
					}

					ul {
						position: absolute;
						display: inline-block;
						top: 0px;
						left: 0px;
						background: #fff;
						padding: 0 22px;
						z-index: 9999;

						li {
							border-bottom: 1px solid #BCBCBC;
							padding: 16px 15px;

							a {
								color: #333;
								font-size: 18px;
							}
						}

						li:nth-child(6) {
							border: 0;
						}

						.menu-active {
							a {
								color: #023B9A;
							}
						}

					}

				}

			}

			.van-col--19 {
				text-align: center;
			}

			.van-col:last-child {
				text-align: right;
			}

			.van-dropdown-menu__bar {
				height: 1;
				background-color: rgba($color: #000000, $alpha: 0);
				box-shadow: 0 0 0 rgba(100, 101, 102, 0);
			}

			.van-dropdown-menu__title {
				overflow: hidden;
			}

			.van-dropdown-menu__title::after {
				border: 0;
			}

			.van-dropdown-item {
				position: absolute;
				height: 784%;
				top: 66px !important;
				border-top: 4px solid #000;
				margin-top: -1px;
				z-index: 999;
			}

			.van-popup {
				width: 42%;
				height: 100%;
			}

			.van-dropdown-item__option--active {
				color: #023B9A;
				text-align: center !important;
			}

			.van-cell::after {
				border: 1px solid #BCBCBC;
			}

			.van-cell__title,
			.van-cell__value {
				-webkit-box-flex: 0;
				-webkit-flex: 0;
				flex: 1;
			}

			.van-cell__value {
				position: absolute;
			}

			.van-overlay {
				background-color: rgba(0, 0, 0, 0.5);
			}

			.van-icon-success::before {
				content: '\F0C8';
				opacity: 0;
			}

			.van-dropdown-item__option {
				text-align: center;
			}

			.van-dropdown-item__content {
				max-height: 100%;
			}
		}
	}

	.bg-show {
		position: fixed;
	}
</style>
